import BookingForm from './components/BookingForm';
import { useEffect, useState } from 'react';

type Booking = {
  id: number;
  code: string;
  datetime: string;
  size: string;
  quantity: number;
  customer: string;
  sale: string;
};

const Home = () => {
  const [bookings, setBookings] = useState<Booking[]>([]);

  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const response = await fetch('/api/bookings');
        if (!response.ok) {
          throw new Error('Failed to fetch bookings');
        }
        const data = await response.json();
        setBookings(data);
      } catch (error) {
        if (error instanceof Error) {
          console.error(error.message);
        }
      }
    };
    fetchBookings();
  }, []);

  return (
    <div>
      <h1>Fish Booking System</h1>
      <BookingForm />
      <h2>All Bookings</h2>
      <ul>
        {bookings.map((booking) => (
          <li key={booking.id}>
            {booking.code} - {new Date(booking.datetime).toLocaleString()} - {booking.size} - {booking.quantity} - {booking.customer} - {booking.sale}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Home;